import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';

import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';
import { NpcService } from 'src/app/services/npc/npc.service';
import { EspecializacoesViewComponent } from '../../utils/especializacoes-view/especializacoes-view.component';
import { EspecializacoesComponent } from '../../utils/especializacoes/especializacoes.component';
import { VantagemViewComponent } from '../../utils/vantagem-view/vantagem-view.component';

@Component({
  selector: 'app-npc-detail',
  templateUrl: './npc-detail.component.html',
  styleUrls: ['./npc-detail.component.scss'],
})
export class NpcDetailComponent implements OnInit {

  @Input('passedNpc')
  passedNpc: any;

  npc;
  loading = true;

  private abaAtual = 'info';

  constructor(
    private modal: ModalController,
    private modalInterno: ModalController,
    private detalharService: DetalharService,
    private npcService: NpcService,
    private toast: MessageService,
  ) {

  }

  ngOnInit() {
    if (this.passedNpc) {
      this.loading = true;
      this.npc = this.passedNpc;
      this.loading = false;
    } else {
      this.loading = true;
      const id = this.detalharService.getId();

      this.npcService.findById(id)
        .subscribe(res => {
          this.npc = res;
          this.loading = false;
        },
          error => {
            console.log(error);
            this.loading = false;
          });
    }


  }


  close() {
    this.modal.dismiss(null, null, "npc-detail");
  }

  mudaAba(event) {
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  getAtributos() {
    return this.npc.ficha.fichaModelo.atributos;
  }

  async openModal(atributo, event) {
    const modal = await this.modalInterno.create({
      component: EspecializacoesViewComponent,
      componentProps: {
        especializacoes: atributo.especializacoes,
        idAtributo: atributo.idAtributo,
        closeButton: true
      },
      backdropDismiss: false

    });

    return await modal.present();
  }

  async openVantagem(event) {
    const modal = await this.modalInterno.create({
      component: VantagemViewComponent,
      componentProps: {
        especializacoes: this.npc.ficha.vantagens,
        idAtributo: this.npc.idNpc,
        closeButton: true,
        titulo: "Vantagens"
      },
      backdropDismiss: false

    });

    return await modal.present();
  }

  async openDesvantagem(event) {
    const modal = await this.modalInterno.create({
      component: VantagemViewComponent,
      componentProps: {
        especializacoes: this.npc.ficha.desvantagens,
        idAtributo: this.npc.idNpc,
        closeButton: true,
        titulo: "Desvantagens"
      },
      backdropDismiss: false

    });

    return await modal.present();
  }

}
